import { ValidatedOptions } from '@patternfly/react-core';
import { debounce } from 'lodash';
import { useState } from 'react';
import { fetchCheckClusterNameAvailable } from 'src/query/deployments';

export function useValidClusterName() {
  const [nameValid, setNameValid] = useState<true | false | null | 'checking'>(null);

  const clearNameValid = () => setNameValid(null);

  const nameValidityResponseText = () => {
    let isNameValid = ValidatedOptions.default;
    let nameHelperText = 'Name must be unique.';
    if (nameValid === false) {
      isNameValid = ValidatedOptions.error;
      nameHelperText = 'Cluster name is not available for use. Please try another.';
    } else if (nameValid) {
      isNameValid = ValidatedOptions.success;
      nameHelperText = 'Cluster name is available for use.';
    }
    if (nameValid === 'checking') {
      nameHelperText = 'Checking for Cluster name availability...';
    }

    return {
      isNameValid,
      nameHelperText,
    };
  };

  const checkAvailabilty = debounce(
    async (nameToCheck: string) => {
      // if more than 2 characters then make API call to check for unique name
      if (nameToCheck.length > 2) {
        setNameValid('checking');

        try {
          const checkValue = await fetchCheckClusterNameAvailable({
            pathParams: { realm: 'self' },
            queryParams: { name: nameToCheck },
          });
          return setNameValid(checkValue.available);
        } catch (e) {
          console.log(e);
        }
      }
      setNameValid(null);
    },
    300,
    {
      maxWait: 1000,
    }
  );

  return {
    checkAvailabilty,
    nameValidityResponseText,
    clearNameValid,
  };
}
